import { useContext } from 'react'
import $ from 'jquery';

import { ControlContext } from '../App';


function Button( { bank } ) {
    const Control = useContext(ControlContext);
    const { volume, isTurnedOn } = Control;
    function buttonClick(e){
        if(isTurnedOn){
            $(e.target).addClass("bg-blue-500 shadow-inner")
            const audio = e.target.lastChild;
            audio.volume = volume;
            audio.currentTime = 0;  
            audio.play().then(()=>{    
                setTimeout(() => {
                    $(e.target).removeClass("bg-blue-500 shadow-inner")
                }, 100);
                
        });
        }
    }

    return (
        <button
        className="drum-pad bg-blue-100 font-bold text-green-900 text-xl rounded-xl select-none outline-none shadow-md transition-colors "
        id={bank.id.replace(/'/g,'')}
        onClick={buttonClick}>  
            {bank.keyTrigger}
            
            <audio id={bank.keyTrigger} className="clip" src={bank.url}></audio>
        </button>
    )
}

export default Button
